import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import '../styles/Navigation.css'
import { Navbar, Nav, NavDropdown, Button } from 'react-bootstrap'
import { Person } from 'react-bootstrap-icons';
import $ from 'jquery';

$(function () {
    $(window).on('scroll', function () {
        if ($(window).scrollTop() > 30) {
            $('.navbar').addClass('active');
        } else {
            $('.navbar').removeClass('active');
        }
    });
});

class Navigation extends Component {
    state = {
        logged: localStorage.getItem("logged")
    }

    logout = () => {
        localStorage.clear();
        this.setState({
            logged: false
        })
    }

    render() {
        //console.log(this.state.logged);
        return (
            <div>
                <Navbar expand="md" fixed="top">
                    <Navbar.Brand to="/home">Cracov City</Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="ml-auto">
                            <Nav.Link href="/home">Home</Nav.Link>
                            <Nav.Link href="/protecteddata">Protected Data</Nav.Link>
                            <Nav.Link href="/explore">Explore</Nav.Link>
                            <Nav.Link href="/things">Things To Do</Nav.Link>
                            {this.state.logged && <NavDropdown title="Control Panel" id="basic-nav-dropdown">
                                <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                                <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                                <NavDropdown.Item href="#action/3.3">Click me please bro</NavDropdown.Item>
                                <NavDropdown.Divider />
                                <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
                            </NavDropdown>}
                            {!this.state.logged ? <Button className="navbtn"><Nav.Link href="/login">
                                <Person className="personIcon" />  Login</Nav.Link>
                            </Button> : <Button className="navbtn" onClick={this.logout}>
                                    <Nav.Link href="/home">
                                        <Person className="personIcon" />  Logout</Nav.Link>
                                </Button>}
                        </Nav>
                    </Navbar.Collapse>
                </Navbar>

            </div>
        );

    }
}

export default Navigation;
