import React, { Component } from 'react';
import RegisterForm from '../components/RegisterForm'
import LoginForm from '../components/LoginForm'

class AccessForms extends Component {
    state = { registerForm: true, }

    handleSwitchForm = () => {
        this.setState({
            registerForm: !this.state.registerForm
        })
    }
    render() {
        return (<div>
            {this.state.registerForm ? <LoginForm changeForm={this.handleSwitchForm} handleLogged={this.props.handleLogged} /> : <RegisterForm changeForm={this.handleSwitchForm} />}
        </div>)
    }
}

export default AccessForms;