import React from 'react';
import { Route, Switch } from 'react-router-dom';
import HomePage from '../pages/HomePage'
import withAuth from '../components/withAuth'
import ProtectedData from '../pages/ProtectedData';
import AccessForms from '../layouts/AccessForms'
import ExplorePage from '../pages/ExplorePage'
import ThingToDoPage from '../pages/ThingsToDoPage'

const Page = (props) => {

    return (
        <div>
            <Switch>
                <Route path="/Cracov-Information-System-Client/" exact component={() => < HomePage />} />
                <Route path="/Cracov-Information-System-Client/home" component={HomePage} />
                <Route path="/Cracov-Information-System-Client/protecteddata" component={withAuth(ProtectedData)} />
                <Route path="/Cracov-Information-System-Client/explore" component={() => < ExplorePage />} />
                <Route path="/Cracov-Information-System-Client/things" component={() => < ThingToDoPage />} />
                <Route path="/Cracov-Information-System-Client/login" component={() => < AccessForms handleLogged={props.handleLogged} />} />
            </Switch>
        </div >

    );
}

export default Page;