import React, { Component } from 'react';
import { Link } from 'react-router-dom'
import '../styles/LoginRegisterForm.css'
import { People } from 'react-bootstrap-icons';
class RegisterForm extends Component {
    state = {}

    render() {

        return (

            <div className="modal" >
                <form className="modal-content animate" >

                    <div className="imgcontainer">
                        {/* <img src="" alt="Avatar" class="avatar" /> */}
                        < People className="avatar" size="150px" />
                    </div>

                    <div className="container">
                        <label ><b>Username</b></label>
                        <input type="text" placeholder="Enter Username" name="uname" required />
                        <label ><b>Name</b></label>
                        <input type="text" placeholder="Enter Username" name="uname" required />
                        <label ><b>Password</b></label>
                        <input type="text" placeholder="Enter Password" name="psw" required />
                        <label ><b> Confirm Password</b></label>
                        <input type="text" placeholder="Enter Password" name="psw" required />
                        <button className="loginbutton" type="submit">Register</button>

                    </div>
                    <div className="container  containerbotton">
                        <Link to="/"><button className="loginbutton cancelbtn" >Close</button></Link>
                        <button className="loginbutton changeformbtn" onClick={this.props.changeForm}>Login</button>
                    </div>
                </form>

            </div>

        );
    }
}

export default RegisterForm;